
/**
 * ui
 * @type {{mutations: {updateContents(*, *): void}, state: (function(): {contents: [{extData: {domain: string, pwd: string, username: string}, name: string, href: string, type: string}]}), getters: {}}}
 */
const ui = {
    namespaced: true,
    state: () => ({
        openHistory: [],//
        //
        itemSize: 120,
        filters:[],//
        config:{
          showSubFolders:true //
        },
        //
        currentTab: {
            name: 'all',
            alias: '',
            path: ''
        },
        /**
         * 
         */
        currentFolder: {
            path: '',
            name: ''
        },
        /**
         * 
         */
        // selectedContentInfo: {
        //   cover: '',
        //   name: '',
        //   remark: '',
        //   href: 'http://apps.vip',
        //   rate: 4.5,
        //
        //   //
        //   baseInfo: {
        //     filename: '',
        //     path: '',
        //     createTime: Date.now(),
        //     modifyTime: Date.now(),
        //     addTime: Date.now(),
        //   }
        // }
    }),
    mutations: {
        setFilter(state,ft){
            state.filters=ft

        },
        addFilter(state,ft){
            state.filters.push(ft)
        },
        pushHistory(state, path) {
            state.openHistory.push(path)
        },
        popHistory(state) {
            state.openHistory.pop()
        },

        /**
         * itemSize
         * @param state
         * @param size
         */
        setItemSize(state, size) {
            state.itemSize = size
        },
        /**
         * tab
         * @param state
         * @param tab
         */
        setTab(state, tab) {
            state.currentTab = tab
        },
        setCurrentFolder(state, folder) {
            state.currentFolder = folder
        },
        /**
         * 
         * @param state
         * @param item
         */
        focusItem(state, item) {
            if(item.data.meta.new){
                item.data.meta.new=false
                window.libDb.setNotNew(item.data.meta.uuid)
            }
            state.focusedItem=item
        },
        setConfig(state,config){
            state.config=Object.assign(state.config,config)
        }
    },
    getters: {},
    actions: {
        init(context) {
            context.dispatch('subFolder/updateTreeData', null, {root: true})
        },
        goBackFolder(context) {
            if (context.state.openHistory.length <2) {
                return
            }
            let backPath=context.state.openHistory[context.state.openHistory.length-2]
            context.commit('popHistory')
            context.commit('popHistory')
            context.dispatch("changeFolder", backPath)
        },
        reload(context,callback){
            window.libDb.reload()
            context.dispatch('content/loadFolderContents',context.state.currentFolder.path,{root:true}) //
            if(callback){
                callback()
            }
        },
        changeFolder(context, folder) {
            context.dispatch('content/loadFolderContents',folder.path,{root:true}) //
            context.commit('setCurrentFolder', folder) //
            context.commit('pushHistory', folder) //
        },
        changeTab(context, tab) {
            switch (tab.name) {
                case 'all':
                    tab.alias = ''
                  context.commit('setFilter',[])
                    //todo 
                    context.dispatch('changeFolder', {
                        relativePath: '/',
                        path:context.rootState.config.storePath,
                        name: ''
                    })
                break
                case 'bookmark':
                    tab.alias = ''
                    //todo 
                break
                case 'img':
                    tab.alias=''
                break
                case 'video':
                    tab.alias=''
                    break
                case 'doc':
                    tab.alias=''
            }
            context.dispatch('changeFolder', {
                relativePath: '/',
                path:context.rootState.config.storePath,
                name: ''
            })
            context.commit('setTab', tab)
        }
    }
}

export default ui
