<template>
  <div class="content-info" >
    <div style="margin-bottom: 20px;height: auto;position: relative">
      <a-image v-if="['img','url'].indexOf(focusedItem.type)>-1"
        :width="200" style="max-height: 300px"
        :src="cover"
      >
        <template #placeholder>
          <a-image
            src="https://zos.alipayobjects.com/rmsportal/jkjgkEfvpUPVyRjUImniVslZfWPnJuuZ.png?x-oss-process=image/blur,r_50,s_50/quality,q_1/resize,m_mfit,h_200,w_200"
            :width="200"
            :preview="false"
          />
        </template>
      </a-image>
      <a-image v-else :style="{objectFit:'cover',padding:20+'px'}" :width="200" :height="190"
               :preview="false" style="background: white;"
               class="sub-folder-cover"
               :src="'./img/type/other/'+focusedItem.type+'.svg'">
      </a-image>
      <img v-if="focusedItem.extData.favicon" onerror="this.src='./img/default.svg'" :src="focusedItem.extData.favicon" class="favicon" style="bottom: -10px;right: -10px">
    </div>
    <div style="margin-bottom: 10px">
      <a-textarea v-model:value="focusedItem.name" :autSize="{ minRows: 1, maxRows: 5 }"  style="margin-bottom: 10px" placeholder=""/>
      <a-select
        :options="options"
        mode="tags"
        placeholder=""
        style="width: 100%"
      ></a-select>
      <a-input v-model:value="focusedItem.remark" placeholder=""/>
      <a-input style="margin-bottom: 0" v-model:value="focusedItem.href" placeholder="">
        <template #suffix>
          <ExportOutlined title="" @click="ipc.addTab(focusedItem.href)"/>
        </template>
      </a-input>
      <a-rate style="margin-bottom: 10px" :value="4" allow-half/>
      <a-select
        :options="options"
        mode="tags"
        placeholder=""
        style="width: 100%"
      ></a-select>
    </div>
    <div style="margin-bottom: 10px">
      <h4></h4>
      <div>
        {{ this.dateStr(focusedItem.baseInfo.createTime) }}
        <br>
        {{ this.dateStr(focusedItem.baseInfo.addTime) }}
        <br>
        {{ this.dateStr(focusedItem.baseInfo.modifyTime) }}
        <br>
        <br><span title="" @click="ipc.showItemInFolder(focusedItem.baseInfo.path+focusedItem.baseInfo.filename)" style="color: #999;cursor:pointer;">{{ focusedItem.baseInfo.relativePath }}{{focusedItem.baseInfo.filename}}</span>
        <br/>
        {{focusedItem.baseInfo.sizeText}}
      </div>
    </div>
    <div>
      <a-button block></a-button>
    </div>
  </div>

</template>

<script>
import { ref, defineComponent } from 'vue'
import {ExportOutlined} from '@ant-design/icons-vue'
import { mapState } from 'vuex'
import ipc from '@/utils/ipc'

export default defineComponent({
  name: 'FocusItemContent',
  props: {},
  components:{
    ExportOutlined
  },
  data () {
    return {
      ipc,
      value3: ref(['a1', 'b2']),
      options: [
        { value: '' },
        { value: '' },
        { value: '' },
        { value: '' },
      ],
    }
  },
  methods: {
    dateStr (date) {
      //js 
      var time = new Date().getTime()
      date=parseInt(date.toFixed(0))/1000
      // js php 
      time = parseInt((time - date * 1000) / 1000)
      //
      var s
      if (time < 60 * 10) {//
        return ''
      } else if ((time < 60 * 60) && (time >= 60 * 10)) {
        //1
        s = Math.floor(time / 60)
        return s + ''
      } else if ((time < 60 * 60 * 24) && (time >= 60 * 60)) {
        //124
        s = Math.floor(time / 60 / 60)
        return s + ''
      } else if ((time < 60 * 60 * 24 * 3) && (time >= 60 * 60 * 24)) {
        //13
        s = Math.floor(time / 60 / 60 / 24)
        return s + ''
      } else {
        //3
        var date2 = new Date(parseInt(date) * 1000)
        return date2.getFullYear() + '/' + (date2.getMonth() + 1) + '/' + date2.getDate()
      }
    }
  },
  computed: {
    ...mapState(
      {
        focusedItem: state => state.ui.focusedItem.data
      }),
    cover(){
      if(this.focusedItem.cover){
        if(this.focusedItem.cover.startsWith('/')){
          return 'file://'+this.$store.state.config.storePath+this.focusedItem.cover
        }else{
          return this.focusedItem.cover
        }
      }else{
        return ''
      }

    }
  },
  setup () {
    return {
      random: ref(),
    }
  }
})
</script>

